<template>
	<div id="collection">
		<van-nav-bar fixed style="z-index:1000">
			<div slot="title">
				收藏
			</div>
      <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
      <div slot="right" v-on:click="isShow">
        {{actionTitle}}
      </div>
		</van-nav-bar>
		<div id="container" >
      <van-list
        v-model="loading"
        :finished="finished"
        :offset="100"
        :immediate-check="false"
      >
			  <div class="list">
          <div class="good-list" v-bind:class="{ good_action: action }" v-for="(item,items) in datas" :key="items">
            <img :src="item.thumb"/>
            <div>
              <span style="text-align: justify;word-break:break-all;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;overflow:hidden;">{{item.title}}</span>
              <div><span>￥{{item.coupon_price}}</span><span>原价 <del>￥{{item.origin_price}}</del></span></div>
              <div class="lq">
                <div><img src="/static/img/lq.png"/><span>领券{{item.coupon_price}}元</span></div>
                <!--<span>已售{{item.collection_id}}万</span>-->
              </div>
              <div class="yg">
                <div>预估赚￥5.20</div>
                <div>升级赚￥5.20</div>
              </div>
              <div style="padding-left: 138px;color: red;" v-if="showoff" v-on:click="deletes(items),cancels(item.id)">删除</div>
            </div>
            <div class="check">
            	<van-checkbox v-model="isCheck[k]" v-if="checkShow" checked-color="#ff5555"></van-checkbox>
            </div>
          </div>
      </div>
        <div class="error" style="background-color:red;color:white;padding: 0.2rem" @click="reload" v-if="isReload">加载失败！点击重新加载</div>
      </van-list>
		</div>

		<van-submit-bar
		  button-text="删除"
      class="sub_bar"
      v-if="checkShow"
      @submit="onDelete"
		>
		<van-checkbox v-model="c"  style="margin-left:10px" checked-color="#ff5555">全选</van-checkbox>
		</van-submit-bar>


	</div>
</template>

<script>
    export default {
        name: "collection",
        data() {
        	return {
            showoff:false,
        		c:false,
            checkShow:false,
            actionTitle:'编辑',
            action: false,
            pageNum:1,
            pageSize: 10,
            list:[],
            loading:false,
            finished:false,
            uid:'2018Xg10258s14384g38',
            isReload:false,
            isCheck:[],
            datas:[],
            idDelete:false,
        	}
        },
        mounted(){
          this.selectCollection();
        },
        methods: {
          selectCollection (){
            var self = this
            this.$POST('/user/scsp',true).then(res =>{
              console.log(res);
              if (res.code === 200) {
                self.datas = res.data
              }
            }).catch(res => {
              this.$toast('连接服务器失败!');
              console.log(res);
            })
          },
          isShow () {
            this.showoff = !this.showoff;
          },
          cancels( ids ){
            let params = {
              id:ids,
            }
            this.$POST('/user/scdel',params,true).then(res =>{
              console.log(res);
              if (res.code === 200) {
                this.$toast(res.msg);
              }
            }).catch(res => {
              this.$toast('连接服务器失败!');
              console.log(res);
            })
          },
          deletes( index ){
            this.datas.splice(index,1);
          },
        },
    }
</script>

<style scoped>

  #collection{
  	font-size: 1rem;
  	background-color: #eeeeee;
  }

 #container{
 	width: 100%;
 	height: 100%;
 	overflow: hidden;
  z-index: 999;
  margin-top: 40px;
 }
  .check{
	position: absolute;
	left: 0.5rem;
	top: 2.5rem;
	z-index: 10;
  }
  .good-list{
    width: 95%;
    text-align: left;
    margin: 0 auto;
    margin-bottom: 10px;
    background-color: #ffffff;
    position: relative;
    z-index: 1;
    padding: 0.5rem;
    transition:padding-left 0.3s;
  }

  .good_action{
    padding-left: 3rem;
  }

  .good-list:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .list:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .good-list>img{
    float: left;
    width: 50%;
  }
  .good-list>div{
    width: 48%;
    float: left;
    text-align:left;
    margin-left:2%;
  }
  .good-list>div>span{
    font-family: 黑体;
    font-size: 18px;
    font-weight: bold;
  }
  .good-list>div>div>span:first-child {
    font-size: 20px;
    color:#e12532;
  }
  .good-list>div>div>span:last-child{
    font-size: 13px;
    float:right;
    line-height: 26px;
    color: #a8a9a9;
  }
  .good-list>div>div{
    margin-top: 15px;
  }
  .good-list>div>div:after{
    content:"";
    display:block;
    height:0;
    clear:both;
    visibility:hidden;
  }
   .lq{
    width: 100%;
  }
  .lq:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .lq>div{
    width: 45%;
    position: relative;
  }
  .lq>div:first-child{
    float: left;
    height:30px;
  }
  .lq>div>img{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    display: block;
    width: 100%;
  }
  .lq>div>span{
    z-index: 1;
    text-align: right;
    line-height:25px;
    position: absolute;
    right:5px;
    color: #ffffff;
    font-size: 14px;
  }
  .yg{
    width: 100%;
    font-size: 12px;
  }
  .yg>div:first-child{
    float: left;
    padding: 2px 5px;
    background: #ffdcdc;
    color: #e12532;
    border-radius:7px;
  }
  .yg>div:last-child{
    float: right;
    padding: 2px 5px;
    background: linear-gradient(to right,#ff722d, #ff4b26 , #ff2820); /* 标准的语法 */
    border-radius:7px;
    color: #ffffff;
  }
  .list{
    margin-top: 10px;
  }
</style>



